.masterhead {
	padding: 60px 15px 30px;
	// background: $gray-lightest;
	background: aqua;
    // background-image: linear-gradient(to bottom right, aqua, aquamarine);
    background: url(../images/1025-min.jpg) no-repeat top left;
    background-size: cover;
    color: white;

	.logo {
		max-height: 100%;
		max-width: 100%;
	    height: 150px;
		margin-bottom: 30px;
	}

	h1,
	h3 {
		letter-spacing: 0.250em;
		text-transform: uppercase;
		margin-bottom: 20px;
	}

	h1 {
		font-size: 2.7em;
	}

	h3 {
		display: inline-block;
		padding: 15px;
		border: 1px solid;
		color: white
	}

	h4 {
		color: white
	}

	ul {
		list-style: none;
		padding: 20px 0;

		li {
			margin: 0 15px 20px;
			// margin-bottom: 20px;
			display: inline-block;
		}

		a:not(.btn) {
			font-size: 18px;
			color: white;
			text-decoration: none;
			&:hover,
			&:active,
			&:focus {
				padding-bottom: 5px;
				border-bottom: 1px solid;
			}
		}
	}
}


.retroshadow {
	font-family: 'Quicksand', sans-serif;
	color: white;
	text-shadow: 1px 1px 0px #de5fd0, 5px 4px 0px rgb(152, 0, 87);
}